@import "../common/reset";
@import "../common/core";

body {
  font-size: 22px !important;
}
.outbx {
  @include defaultAreaSet;
}
main {
  width: 100%;
}
main > div {
  overflow: hidden;

  max-width: 1400px;
  margin: 0 auto;
}
// .main_0 {
//   line-height:
// }
.main_0 h1 {
  margin-bottom: 5%;
}

main > div {
  overflow: hidden;
  max-width: 1400px;
  margin: 0 auto;
}

.main_0 {
  margin-bottom: 15%;
}
.main_0:nth-of-type(2) img {
  margin-top: 15%;
}

header {
  display: flex;
  justify-content: flex-end;
}

.main_01 {
  max-width: -moz-fit-content;
  max-width: fit-content;
}
.main_01 img {
  max-width: auto;
  width: 100%;
}
.main_01 div {
  color: #998465;
  position: relative;
}
.main_01 div p {
  position: absolute;
  top: 0;
}
.main_01 div .care {
  position: absolute;
  bottom: 5%;
  right: 3%;
  text-align: right;
  color: #fff;
}

.main_02 {
  display: flex;
}
.main_02 > * {
  flex: 1;
}
.main_02 img {
  width: 100%;
}
.main_02 div {
  margin: 12% 0;
}

.main_02 .txt-inbox {
  border-left: 4px solid #ddd;
  padding-left: 20px;
  // margin-left: 20px;
  width: 60%;
  margin: 30% 0 0 8%;
}

.main_03 {
  display: flex;
}
.main_03 img {
  margin-left: 10%;
  width: 45%;
}
.main_03 div {
  margin: 5% 0 0 3%;
  // padding-left: 5%;
  width: 55%;
}
.main_03 div div {
  border-left: 4px solid #ddd;
  padding-left: 3%;
}

.main_04 {
  display: flex;
}
.main_04 img {
  transform: translate(25%);
  width: 70%;
}
.main_04 span {
  margin: 10% 0;
  transform: translate(50%, 0%);
  width: 30%;
  text-align: right;
  padding-right: 3%;
}

.main_04 .txt-inbox {
  border-right: 4px solid #ddd;
  padding-right: 20px;
}

.main_05 ul {
  display: flex;
  text-align: center;
  gap: 10px;
}
.main_05 ul li {
  width: 33.3%;
}
.main_05 ul li img {
  width: 100%;
}
.main_05 h1 {
  text-align: center;
}
.main_05 h1::after {
  content: "";
  transform: translate(-50%, 0);
  margin: 10px 0 10% 50%;
  display: block;
  width: 10%;
  border-bottom: 1px solid #000000;
}

.main_06 .rel {
  position: relative;
}
.main_06 .rel .swiper {
  width: 100%;
}
.main_06 .rel div {
  position: absolute;
  width: 20%;
  height: 50%;
  background-color: #fff;
  margin-left: 10%;
  top: 0;
}
.main_06 .rel .left {
  position: absolute;
  left: 1%;
  top: 50%;
  transform: translate(0%, -50%);
  width: 3%;
}
.main_06 .rel .right {
  position: absolute;
  right: 1%;
  top: 50%;
  transform: translate(0%, -50%);
  width: 3%;
}

@media (max-width: 1024px) {
  .main_05 li img {
    width: 90%;
  }
}
@media (max-width: 768px) {
  .main_02 div {
    transform: translate(0%);
    border-left: none;
  }
  .main_02 div:nth-of-type(2) {
    margin: 0;
  }
  .main_02 .txt-inbox {
    margin: 0;

    width: 100%;
    padding-left: 0px;
    text-align: center;
    border-left: none;
  }
  .main_02 img {
    transform: none;
  }
  .main_03 div {
    margin: 0;
    padding: 0;
  }
  .main_0 {
    text-align: center;
    display: flex;
    flex-direction: column;
    img {
      width: 70%;
      margin: 0;
    }
    margin: 0;
    align-items: center;
  }
  .main_04 {
    flex-direction: column-reverse;
  }
  .main_04 span {
    transform: translate(00%, 0%);
    width: 100%;
    text-align: center;
  }
  .main_04 img {
    transform: none;
  }
  .main_05 {
    display: block;
  }
  .main_05_inbox {
    overflow-x: scroll;
  }
  .main_05 ul {
    width: 300vw;
  }
  .main_05 ul li {
    width: 100%;
    // margin-bottom: 7%;
  }
  .main_05 ul li img {
    // width: 50%;
    margin-bottom: 3%;
  }
  .main_06 .rel div {
    width: 40%;
    height: 70%;
  }
}
