.main_07 {
  padding-bottom: 10vh;
  ul {
    width: 80%;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    gap: 10px;
    text-align: center;
    li {
      flex: 1;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
